import React from 'react';
import { useSelector, batch } from 'react-redux';
import { increaseAgility, decreaseAgility } from './traitsSlice';
import { increaseAttribute, decreaseAttribute } from '../points/pointsSlice';
import { useAppDispatch, RootState } from '../../reducers';
import { GiAcrobatic } from 'react-icons/gi';
import { errorToast } from '../../components/Toast';
import TraitCard from '../../components/TraitCard';
import TraitButtons from '../../components/TraitCard/TraitButtons';

const Agility = () => {
	const name = useSelector((state: RootState) => state.traits.agility.name);
	const agilityValue = useSelector((state: RootState) => state.traits.agility.value);
	const skills = useSelector((state: RootState) => state.traits.agility.skills);
	const attrValue = useSelector((state: RootState) => state.points.attribute.value);
	const dispatch = useAppDispatch();

	const handleIncrease = () => {
		if (attrValue === 0) {
			errorToast("Out of Attribute Points!");
			return;
		}
		batch(() => {
			dispatch(increaseAgility());
			dispatch(increaseAttribute());
		});
	};

	const handleDecrease = () => {
		batch(() => {
			dispatch(decreaseAgility());
			dispatch(decreaseAttribute());
		});
	};

	return (
		<TraitCard
			traitName={name}
			traitValue={agilityValue}
			TraitIcon={GiAcrobatic}
			skills={skills}>
			<TraitButtons
				stat={agilityValue}
				handleDecrease={handleDecrease}
				handleIncrease={handleIncrease}
			/>
		</TraitCard>
	);
};

export default Agility;
