import React from 'react';
import { useSelector, batch } from 'react-redux';
import { increaseSpirit, decreaseSpirit } from './traitsSlice';
import { increaseAttribute, decreaseAttribute } from '../points/pointsSlice';
import { useAppDispatch, RootState } from '../../reducers';
import { GiMagicPalm } from 'react-icons/gi';
import { errorToast } from '../../components/Toast';
import TraitCard from '../../components/TraitCard';
import TraitButtons from '../../components/TraitCard/TraitButtons';

const Spirit = () => {
	const name = useSelector((state: RootState) => state.traits.spirit.name);
	const spiritValue = useSelector((state: RootState) => state.traits.spirit.value);
	const skills = useSelector((state: RootState) => state.traits.spirit.skills);
	const attrValue = useSelector((state: RootState) => state.points.attribute.value);
	const dispatch = useAppDispatch();

	const handleIncrease = () => {
		if (attrValue === 0) {
			errorToast("Out of Attribute Points!");
			return;
		}
		batch(() => {
			dispatch(increaseSpirit());
			dispatch(increaseAttribute());
		});
	};

	const handleDecrease = () => {
	
		batch(() => {
			dispatch(decreaseSpirit());
			dispatch(decreaseAttribute());
		});
	};

	return (
		<TraitCard
			traitName={name}
			traitValue={spiritValue}
			TraitIcon={GiMagicPalm}
			skills={skills}>
			<TraitButtons
				stat={spiritValue}
				handleDecrease={handleDecrease}
				handleIncrease={handleIncrease}
			/>
		</TraitCard>
	);
};

export default Spirit;
