import React from 'react';
import { Route, Switch } from 'react-router-dom';

import Home from '../pages/Home';
import NotFound from '../pages/NotFound';

const Routes = () => {
	return (
		<Switch>
			<Route exact path={process.env.PUBLIC_URL + '/'}>
				<Home />
			</Route>
			<Route>
				<NotFound />
			</Route>
		</Switch>
	);
};

export default Routes;
