import { createSlice } from '@reduxjs/toolkit';

const initialState = {
	agility: {
		name: 'Agility',
		value: 4,
		skills: {
			athletics: 4,
			boating: 0,
			driving: 0,
			fighting: 0,
			piloting: 0,
			riding: 0,
			shooting: 0,
			stealth: 0,
			thievery: 0
		}
	},
	smarts: {
		name: 'Smarts',
		value: 4,
		skills: {
			academics: 0,
			battle: 0,
			knowledge: 4,
			electronics: 0,
			gambling: 0,
			hacking: 0,
			healing: 0,
			notice: 0,
			occult: 0,
			taunt: 0,
			survival: 0
		}
	},
	spirit: {
		name: 'Spirit',
		value: 4,
		skills: {
			faith: 0,
			focus: 0,
			intimidation: 0,
			performance: 0,
			persuasion: 0
		}
	},
	strength: {
		name: 'Strength',
		value: 4
	},
	vigor: {
		name: 'Vigor',
		value: 4
	}
};

const traitsSlice = createSlice({
	name: 'traits',
	initialState,
	reducers: {
		increaseAgility: state => {
			state.agility.value += 2;
		},
		decreaseAgility: state => {
			state.agility.value -= 2;
		},
		increaseSmarts: state => {
			state.smarts.value += 2;
		},
		decreaseSmarts: state => {
			state.smarts.value -= 2;
		},
		increaseSpirit: state => {
			state.spirit.value += 2;
		},
		decreaseSpirit: state => {
			state.spirit.value -= 2;
		},
		increaseStrength: state => {
			state.strength.value += 2;
		},
		decreaseStrength: state => {
			state.strength.value -= 2;
		},
		increaseVigor: state => {
			state.vigor.value += 2;
		},
		decreaseVigor: state => {
			state.vigor.value -= 2;
		}
	}
});

export const {
	increaseAgility,
	decreaseAgility,
	increaseSmarts,
	decreaseSmarts,
	increaseSpirit,
	decreaseSpirit,
	increaseStrength,
	decreaseStrength,
	increaseVigor,
	decreaseVigor
} = traitsSlice.actions;

export default traitsSlice.reducer;
