import { createSlice } from '@reduxjs/toolkit';

const initialState = {
	attribute: {
		name: 'Attributes',
		value: 5,
		max: 5
	},
	skill: {
		name: 'Skills',
		value: 12,
		max: 12
	},
	edge: {
		name: 'Edges',
		value: 1,
		max: 1
	},
	wealth: {
		name: 'Wealth',
		value: 500,
		max: 500
	},
	load: {
		name: 'Load',
		value: 0,
		max: 500
	}
};

const pointsSlice = createSlice({
	name: 'points',
	initialState,
	reducers: {
		increaseAttribute: state => {
			state.attribute.value--;
		},
		decreaseAttribute: state => {
			state.attribute.value++;
		},
		increaseSkill: state => {
			state.skill.value--;
		},
		decreaseSkill: state => {
			state.skill.value++;
		},
		increaseEdge: state => {
			state.edge.value--;
		},
		decreaseEdge: state => {
			state.edge.value++;
		},
		increaseWealth: (state, payload) => {
			state.wealth.value = -payload;
		},
		decreaseWealth: (state, payload) => {
			state.wealth.value = +payload;
		}
	}
});

export const {
	increaseAttribute,
	decreaseAttribute,
	increaseSkill,
	decreaseSkill,
	increaseEdge,
	decreaseEdge,
	increaseWealth,
	decreaseWealth
} = pointsSlice.actions;

export default pointsSlice.reducer;
