import React from 'react';
import { useSelector, batch } from 'react-redux';
import { increaseSmarts, decreaseSmarts } from './traitsSlice';
import { increaseAttribute, decreaseAttribute } from '../points/pointsSlice';
import { useAppDispatch, RootState } from '../../reducers';
import { GiBrain } from 'react-icons/gi';
import { errorToast } from '../../components/Toast';
import TraitCard from '../../components/TraitCard';
import TraitButtons from '../../components/TraitCard/TraitButtons';

const Smarts = () => {
	const name = useSelector((state: RootState) => state.traits.smarts.name);
	const smartsValue = useSelector((state: RootState) => state.traits.smarts.value);
	const skills = useSelector((state: RootState) => state.traits.smarts.skills);
	const attrValue = useSelector((state: RootState) => state.points.attribute.value);
	const dispatch = useAppDispatch();

	const handleIncrease = () => {
		if (attrValue === 0) {
			errorToast('Out of Attribute Points!');
			return;
		}
		batch(() => {
			dispatch(increaseSmarts());
			dispatch(increaseAttribute());
		});
	};

	const handleDecrease = () => {
		batch(() => {
			dispatch(decreaseSmarts());
			dispatch(decreaseAttribute());
		});
	};

	return (
		<TraitCard traitName={name} traitValue={smartsValue} TraitIcon={GiBrain} skills={skills}>
			<TraitButtons
				stat={smartsValue}
				handleDecrease={handleDecrease}
				handleIncrease={handleIncrease}
			/>
		</TraitCard>
	);
};

export default Smarts;
